

import {defineComponent, inject} from "vue";

export default defineComponent({
  name: "DashboardCard",

  props: {
    subtitle: Boolean
  },

  setup() {
    const isSmallScreen = inject('isSmallScreen', true)
    const isTouchDevice = inject('isTouchDevice', false)

    return { isSmallScreen, isTouchDevice }
  }
})

