import axios, {AxiosResponse} from "axios";
import {Collector} from "@/utils/Collector";
import {CoinGeckoResponse} from "@/schemas/CoinGeckoMarketData";

export class CoinGeckoCollector extends Collector<CoinGeckoResponse, null> {

    public static readonly instance = new CoinGeckoCollector()

    //
    // Collector
    //

    protected load(/* unused: null */): Promise<AxiosResponse<CoinGeckoResponse>> {

        const coinGeckoMarketDataURL = "https://api.coingecko.com/api/v3/coins/hedera-hashgraph"

        const params = {
            market_data: true,
            localization: false,
            tickers: false,
            community_data: false,
            developer_data: false,
            sparkline: false
        }

        return axios.get<CoinGeckoResponse>(coinGeckoMarketDataURL, {params})
    }


}